* {
  box-sizing: border-box;
}

.app{
  background: #eee;
}

@font-face {
  font-family: "BabelStone Xiangqi";
  src: local("BabelStone Xiangqi"), url("./../fonts/BabelStoneXiangqi.ttf") format("truetype");
}

.xq{
  font-family: "BabelStone Xiangqi";
}

.xq-king::before{
  content: "\1FA60";  /*\U+1FA60 -> \1FA60*/
}

@media screen and (min-width:320px)
{
  .container{
    width: 100%;
    overflow: auto;
  } 

  .chess-board{
    font-family: "BabelStone Xiangqi";
    background: #e7e3bb;
    padding: 0 10px;
    width: 100%; /* padding * 2 + 24px * 11=284px*/
    float: left;
    font-size: 24px;
    line-height:24px; /*same font size to reduce space between rows*/
  }

  .layer{
    width: 264px;
    margin: 0 auto;
  }

  .navigation {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
    border: 1px solid;
    padding: 10px;
  }

  .line-number{
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 0;
    padding: 0;
    position: relative;
    font-size: 12px;
  }
  
    .line-number.top{
      bottom: -14px;
    }
  
    .line-number.bottom{
      bottom: 14px;
    }
  
    .line-number .number{
      width: 24px;
      display: inline-block;
      text-align: center;
    }
  
  .square{
    display: inline-block;
    position: relative;
    /* border: 0.5px dotted #fd00ff; */
  }

  .chess{
    background: #e7e3bb;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 16px;
  }

  .navigation button {
    display: inline-block;
    font-size: 36px;
    width: 48px;
    border: 1px solid;
    text-align: center;
    cursor: pointer;
  }

  .history {
    border: 1px solid gray;
    float: left;
    width: 100%;
    padding: 5px;
  }

  .history .moves{
    height: 148px;
    overflow: auto;
    margin: 10px;
  }

  .history ul{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding: 0;
  }

  .history .moves li{
    width: 70px;
    list-style: none;
    margin:0;
    padding:0;
    cursor: pointer;
  }

  .history .moves .current{
    background: #daec9b;
  }

  .tab{
    display: block;
  }

  .tab ul{
    margin:0;
    padding:0;
    list-style: none;
  }

  .tab > ul li{
    display: inline-block;
    border-bottom: 2px solid #9f9f9f;
    padding: 2px 5px;
    color: #9f9f9f;
    cursor: pointer;
    font-weight: 500;
  }

  .tab > ul li.current{
    border-bottom: 2px solid #1578c7;
    color: #333;
  }

  textarea{
    width: 100%;
  }
}

@media screen and (min-width:640px) {
  .chess-board{
    line-height: 48px;
    font-size: 48px;
  }

  .layer{
    width: 528px;
  }

  .line-number{
    font-size:24px;
  }

  .line-number .number{
   width: 48px; 
  }

  input, select, label{
    font-size: 24px;
  }

  .navigation button{
    font-size: 64px;
    width: 72px;
  }

  .tab{
    font-size: 48px;
  }
}

@media screen and (min-width: 800px)
{
  .chess-board{
    font-size: 24px;
    line-height: 24px;
    width: 284px;
  }

  .navigation{
    width: 264px;
  }

  .layer{
    width: 264px;
  }

  .line-number{
    font-size: 12px;
  }

  .line-number .number{
    width: 24px;
  }

  .tab{
    font-size: 12px;
    float: left;
    width: 264px;
  }

  .history{
    width: 264px;
  }

  .fen-editor{
    width: 264px;
  }
}